






































import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

@Component({
    name: "MyDialog"
})
export default class MyDialog extends Mixins(TwoWay) {
    @Prop({ default: false })
    loading!: boolean;

    @Prop({})
    title!: string;

    @Prop({})
    beforeClose!: any;

    clientSwitch = true;

    @Watch("currentValue")
    WatchCurrentValue(val: boolean) {
        if (!val) {
            this.clientSwitch = true;
        }
    }

    mousePosition: any = { clientX: "", clientY: "" };

    /**更新并设置鼠标位置 */
    commitPosition(e: any) {
        if (this.currentValue && this.clientSwitch) {
            this.mousePosition.clientX = e.clientX;
            this.mousePosition.clientY = e.clientY;

            this.clientSwitch = false;
        }
    }
    handlerConfirm() {
        this.$emit("on-confirm");
    }
    handlerCancel() {
        this.$emit("on-cancel");
    }
    handleClose() {
        this.$emit("on-colse");
    }

    beforeDestroy() {
        document.removeEventListener("click", this.commitPosition, false);
        this.$root.$off("dialogClickXY");
    }

    /**注册全局监听函数 */
    mounted() {
        this.$root.$on("dialogClickXY", this.commitPosition);
        document.addEventListener("click", this.commitPosition, false);
    }
}
